html {
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
}

h2 {
  font-size: 1rem;
}

body {
  margin: 0 0 50px;

  text-align: center;

  -webkit-transition: all 0.2s;

  transition: all 0.2s;
}

.night {
  color: white;

  background-color: #242f3e;
}
#btn-switch {
    display: inline-block;

    color: #108be3;

    cursor: pointer;

    -webkit-user-select: none;

       -moz-user-select: none;

        -ms-user-select: none;

            user-select: none;
}
.cb_wrapper {
    display: -webkit-flex;
    display: flex;
    /* fallback for IE */
    width: 8em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 2em;

    border: 1px solid rgba(170, 170, 170, 85);
    border-radius: 1em;

    cursor: pointer;

    -webkit-user-select: none;

       -moz-user-select: none;

        -ms-user-select: none;

            user-select: none;
}

.cb_unchecked-mark, .cb_checked-mark {
    box-sizing: border-box;
    width: 1.2em;
    height: 1.2em;
    margin: 0.4em;
    overflow: hidden;

    border: 1px solid currentColor;
    border-radius: 0.6em;

    color: transparent;

    -webkit-transition: background-color 0.5s;

    transition: background-color 0.5s;
}

.cb_checked-mark {
    background-color: currentColor;
}

.cb_checked-mark .icon {
    top: 0;
}

.cb_label {
    margin: 0.5em 0.5em 0.5em 0;

    font-weight: normal;
}

.icon {
    position: relative;
    top: -2em;

    width: 0.7em;
    height: 0.7em;
    margin: 0.2em;

    color: inherit;

    -webkit-transition: top 0.5s;

    transition: top 0.5s;
}
.crt_wrapper {
    float: left;
    box-sizing: border-box;
    width: calc(100%);
    margin: 0;
    padding: 50px;
}

@media (min-width: 768px) {
    .crt_wrapper {
        width: calc(100% / 2);
    }
}

@media (min-width: 1200px) {
    .crt_wrapper {
        width: calc(100% / 3);
    }
}
